import { useNavigate } from "react-router-dom";
import {
  Coupon,
  MyPageCoupon,
  Screen,
  Brand,
  LeftSide,
} from "../../../../components";
import * as S from "./MyPage.styled";

export function MyPageScreen() {
  const navigate = useNavigate();
  const active = true;

  return (
    <Screen header={true} nav={true} footer={true}>
      <S.Layout>
        <LeftSide />
        <S.Container>
          <S.MainText></S.MainText>
          <S.MyCoupon>
            <S.TextGroup>
              <S.Text>  </S.Text>
              <S.ViewText
                onClick={() => {
                  navigate("./recent");
                }}
              >
                 
              </S.ViewText>
            </S.TextGroup>
            <S.Line />
            <Coupon active={active} />
          </S.MyCoupon>
          <S.MyCoupon>
            <S.TextGroup>
              <S.Text> </S.Text>
              <S.ViewText
                onClick={() => {
                  navigate("./used");
                }}
              >
                 
              </S.ViewText>
            </S.TextGroup>
            <S.Line />
            <MyPageCoupon active={active} order={"usedCoupons"} />
          </S.MyCoupon>
          <S.MyCoupon>
            <S.TextGroup>
              <S.Text> </S.Text>
              <S.ViewText
                onClick={() => {
                  navigate("./wish");
                }}
              >
                 
              </S.ViewText>
            </S.TextGroup>
            <S.Line />
            <MyPageCoupon active={active} order={"likeCoupon"} />
          </S.MyCoupon>
          <S.MyCoupon>
            <S.TextGroup>
              <S.Text> </S.Text>
              <S.ViewText
                onClick={() => {
                  navigate("./");
                }}
              >
                 
              </S.ViewText>
            </S.TextGroup>
            <S.Line />
            <MyPageCoupon active={active} order={"createCoupon"} />
          </S.MyCoupon>
          <S.MyCoupon>
            <S.TextGroup>
              <S.Text></S.Text>
              <S.ViewText
                onClick={() => {
                  navigate("./favor");
                }}
              >
                 
              </S.ViewText>
            </S.TextGroup>
            <S.Line />
            <Brand category={"likeBrand"} />
          </S.MyCoupon>
        </S.Container>
      </S.Layout>
    </Screen>
  );
}
