import React from "react";
import * as S from "./LeftSide.styled";
import { useNavigate } from "react-router-dom";

export function LeftSide() {
  const navigate = useNavigate();
  const [isOpen, setIsOpen] = React.useState<boolean>(false);
  const [clickedIndex, setClickedIndex] = React.useState<number | null>(null);

  const onToggle = () => setIsOpen(!isOpen);
  const onOptionClicked = (value: string, index: number) => () => {
    console.log(value);
    console.log(index);
    // setIsOpen(false);
    setClickedIndex(index);
  };

  return (
    <S.Container>
      <S.Info onClick={onToggle}>
        <p> </p>
      </S.Info>
      <S.DropDownContainer>
        {isOpen && (
          <>
            <S.Line />
            <S.ListItem
              onClick={() => {
                onOptionClicked(" ", 1);
                navigate("/user/help/profile");
              }}
              clicked={clickedIndex === 1}
            >
               
            </S.ListItem>
            <S.ListItem
              onClick={() => {
                onOptionClicked(" ", 2);
                navigate("/user/help/profile");
              }}
              clicked={clickedIndex === 2}
            >
               
            </S.ListItem>
            <S.ListItem
              onClick={() => {
                onOptionClicked("", 3);
                navigate("/user/help/profile"); // 
              }}
              clicked={clickedIndex === 3}
            >
              
            </S.ListItem>
          </>
        )}
      </S.DropDownContainer>
      <S.CategoryMenuBox
        onClick={() => {
          navigate("/user/me/recent"); // 
        }}
      >
        <p>  </p>
      </S.CategoryMenuBox>
      <S.CategoryMenuBox
        onClick={() => {
          navigate("/user/me/recent");
        }}
      >
        <p> </p>
      </S.CategoryMenuBox>
      <S.CategoryMenuBox
        onClick={() => {
          navigate("/user/me/wish");
        }}
      >
        <p> </p>
      </S.CategoryMenuBox>
      <S.CategoryMenuBox
        onClick={() => {
          navigate("/user/me"); //   
        }}
      >
        <p> </p>
      </S.CategoryMenuBox>
    </S.Container>
  );
}

export default LeftSide;
