import React, { useState } from "react";
import * as S from "./Nav.styled";
import useDetectClose from "./useDetectClose";
import { Link } from "react-router-dom";

export const Nav: React.FC = () => {
  // useDetectClose -> ,   
  const [FoodIsOpen, FoodRef, FoodHandler] = useDetectClose(false);
  const [CultureIsOpen, CultureRef, CultureHandler] = useDetectClose(false);
  const [ShopIsOpen, ShopRef, ShopHandler] = useDetectClose(false);

  const [selectedItem, setSelectedItem] = useState<string | null>(null);

  const handleMenuItemClick = (categoryName: string) => {
    setSelectedItem(categoryName);
  };

  return (
    <>
      <S.Container>
        <S.DropdownContainer>
          <S.DropdownButton onClick={FoodHandler} ref={FoodRef}>
            
          </S.DropdownButton>
          <S.Menu isDropped={FoodIsOpen}>
            <S.Ul>
              <S.Li isSelected={selectedItem === ""}>
                <Link to={"/coupon/category/"}></Link>
              </S.Li>
              <S.Li isSelected={selectedItem === ""}>
                <Link
                  style={{ textDecoration: "none" }}
                  to={"/coupon/category/"}
                  onClick={() => handleMenuItemClick("")}
                >
                  
                </Link>
              </S.Li>
            </S.Ul>
          </S.Menu>
        </S.DropdownContainer>

        <S.DropdownContainer>
          <S.DropdownButton onClick={CultureHandler} ref={CultureRef}>
            
          </S.DropdownButton>
          <S.Menu isDropped={CultureIsOpen}>
            <S.Ul>
              <S.Li isSelected={selectedItem === ""}>
                <Link
                  to={"/coupon/category/"}
                  onClick={() => handleMenuItemClick("")}
                >
                  
                </Link>
              </S.Li>
              <S.Li isSelected={selectedItem === ""}>
                <Link
                  to={"/coupon/category/"}
                  onClick={() => handleMenuItemClick("")}
                >
                  
                </Link>
              </S.Li>
            </S.Ul>
          </S.Menu>
        </S.DropdownContainer>

        <S.DropdownContainer>
          <S.DropdownButton onClick={ShopHandler} ref={ShopRef}>
            
          </S.DropdownButton>
          <S.Menu isDropped={ShopIsOpen}>
            <S.Ul>
              <S.Li isSelected={selectedItem === ""}>
                <Link
                  to={"/coupon/category/"}
                  onClick={() => handleMenuItemClick("")}
                >
                  
                </Link>
              </S.Li>
              <S.Li isSelected={selectedItem === ""}>
                <Link
                  to={"/coupon/category/"}
                  onClick={() => handleMenuItemClick("")}
                >
                  
                </Link>
              </S.Li>
              <S.Li isSelected={selectedItem === ""}>
                <Link
                  to={"/coupon/category/"}
                  onClick={() => handleMenuItemClick("")}
                >
                  
                </Link>
              </S.Li>
            </S.Ul>
          </S.Menu>
        </S.DropdownContainer>
      </S.Container>
      <S.Line />
    </>
  );
};
